@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@700&family=Readex+Pro&family=Roboto:wght@400;700&display=swap);
.CommentInfo{margin-bottom:20px;padding:12px;background-color:#fff;border-radius:8px}.CommentInfo__email{margin-bottom:8px;font-size:12px;color:#00669d}.CommentInfo__name{font-size:16px;color:#0d6b6f;font-style:italic}

.CommentsList__items{display:flex;flex-direction:column;grid-gap:20px;gap:20px;align-items:center;margin-bottom:20px;padding:16px}

.UserInfo{display:flex;flex-direction:column}.UserInfo__name{color:#839d83}

.PostInfo__user{margin-bottom:20px;display:flex;grid-gap:20px;gap:20px}.PostInfo__title{margin-bottom:20px;text-align:center;text-transform:uppercase;color:#0d6b6f;font-size:24px;font-weight:bold}.PostInfo__body{margin-bottom:40px;font-size:20px}

.PostList{display:flex;flex-direction:column;grid-gap:60px;gap:60px;align-self:center}.PostList__item{margin:0 auto;padding:40px;max-width:60%;background-color:#f2efe9;box-shadow:0 10px 10px rgba(0,0,0,0.1);border-radius:10px}

*{box-sizing:border-box}body,h2,h3,p,li,ul{margin:0}li{list-style:none}html{font-family:"Readex Pro", sans-serif}body{background-color:#626367;color:#626367}

