@import url("https://fonts.googleapis.com/css2?family=Dosis:wght@700&family=Readex+Pro&family=Roboto:wght@400;700&display=swap");

* {
  box-sizing: border-box;
}

body,
h2,
h3,
p,
li,
ul {
  margin: 0;
}

li {
  list-style: none;
}

html {
  font-family: "Readex Pro", sans-serif;
}

body {
  background-color: #626367;
  color: #626367;
}
